import React, { useEffect } from "react";
import jsImg from "../../styles/assets/images/Techstack/Javascript.png";
import reactImg from "../../styles/assets/images/Techstack/React.png";
import angularImg from "../../styles/assets/images/Techstack/Angular.png";
import nodeImg from "../../styles/assets/images/Techstack/Node.png";
import mongoImg from "../../styles/assets/images/Techstack/mongo.png";
import solidityImg from "../../styles/assets/images/FramworksHD/solidity.avif";

const Popular = () => {
  return (
    <section className="section-featured">
      <div className="container">
        <h4 className="rightAnimation heading-fourth servicehead">
          POPULAR IN
        </h4>
        <div className="logos">
          <div className="bottomAnimation featuretech">
            <p className="feature-description">Javascript</p>
            <img src={jsImg} alt="javascript" />
          </div>

          <div className="bottomAnimation featuretech">
            <p className="feature-description">React</p>
            <img src={reactImg} alt="react" />
          </div>
          <div className="bottomAnimation featuretech">
            <p className="feature-description">Angular</p>
            <img src={angularImg} alt="angualr" />
          </div>

          <div className="bottomAnimation featuretech">
            <p className="feature-description">Node</p>
            <img src={nodeImg} alt="node" />
          </div>

          <div className="bottomAnimation featuretech">
            <p className="feature-description">Mongo</p>
            <img src={mongoImg} alt="mongo" />
          </div>

          <div className="bottomAnimation featuretech">
            <p className="feature-description">Solidity</p>
            <img src={solidityImg} alt="solidity" />
          </div>
        </div>
      </div>
    </section>
  );
};

export default Popular;
