import React, { useEffect } from "react";
import bannerImg from "../../styles/assets/images/ServiceLogo/Computer-removebg-preview.png";

const Banner = () => {
  return (
    <section className=" section-hero ">
      <div className="hero">
        <div className="hero-text-box">
          <h1 className="bottomAnimation heading-primary" id="welcome">
            Welcome to Justtry
          </h1>
          <p className="bottomAnimation description text">
            Our team of experienced web developers is dedicated to delivering
            high-quality, user-friendly websites that exceed our client's
            expectations.
          </p>
          <p className="bottomAnimation description text">
            Contact us today to see how we can help bring your vision to life
          </p>

          <p className="bottomAnimation description text">
            Let Justtry take care of the product and processes so you can focus
            on business development
          </p>

          <a href="#cta" className="outer btn btn--full margin-right-sm">
            Get Started
          </a>
          <a href="#how" className="bottomAnimation outer btn btn--outline">
            Learn more &darr;
          </a>
        </div>
        <div className="hero-img-box">
          <img
            className="rightAnimation hero-img"
            src={bannerImg}
            alt="Logo of JT Team Img"
          />
        </div>
      </div>
    </section>
  );
};

export default Banner;
