import React, { useEffect } from "react";
import partzrider from "../../styles/assets/images/recentworks/partzrider.png";
import truefriend from "../../styles/assets/images/recentworks/Truefriend.png";

const RecentWorks = () => {
  return (
    <section className="section-recentworks">
      <div className="container grid">
        <h4 className="rightAnimation heading-fourth servicehead">
          Our Recent Works
        </h4>
      </div>
      <div className="container grid grid--2-cols grid--center-v margin-bottom-md">
        <div className="bottomAnimation work-img-box">
          <img src={partzrider} className="work-img" alt="Recent projects logos" />
        </div>
        <div className="rightAnimation works-text-box">
          <h4 className="heading-fourth subhead">Partzrider</h4>

          <p className="description">
            The portal, Partzrider.com is owned and operated by George Oakes
            Limited, a public company incorporated under the Companies Act,
            1913. George Oakes enables sale and purchase of diverse range of
            products listed on the Platform from time to time.
          </p>
          <a
            className="outer btn btn--full"
            href="https://partzrider.com/"
            target="_blank"
          >
            Visit
          </a>
        </div>

        <div className="bottomAnimation works-text-box">
          <h4 className="heading-fourth servicehead">Truefriend Matrimony</h4>
          <p className="description">
            True Friend Matrimony, a trusted platform to find your life partner.
            Our website is dedicated to helping you find a compatible match who
            shares your faith, values, and aspirations, and will be a great and
            perfect match for your beloved family.
          </p>
          <a
            className="outer btn btn--full"
            href="https://www.truefriendmatrimony.com/"
            target="_blank"
          >
            Visit
          </a>
        </div>
        <div className="work-img-box">
          <img
            src={truefriend}
            className="rightAnimation work-img"
            alt="recentworks logo"
          />
        </div>
      </div>
    </section>
  );
};

export default RecentWorks;
