import React, { useEffect } from "react";
import aboutus from "../../styles/assets/images/ServiceLogo/aboutus.png";
import rebuildImg from "../../styles/assets/images/works1.avif";
import ourTeam from "../../styles/assets/images/works2.avif";

const AboutUs = () => {
  return (
    <section className="section-work" id="blogs">
      <div className="container grid grid--2-cols">
        <div className="work-img-box">
          <img
            className="bottomAnimation work-img"
            src={aboutus}
            alt="Design Logo"
          />
        </div>
        <div className="works-text-box ">
          <p className="bottomAnimation work-number">
            <svg
              xmlns="http://www.w3.org/2000/svg"
              fill="none"
              viewBox="0 0 24 24"
              stroke-width="1.5"
              stroke="currentColor"
              className="work-arrow"
            >
              <path
                stroke-linecap="round"
                stroke-linejoin="round"
                d="M19.5 13.5L12 21m0 0l-7.5-7.5M12 21V3"
              />
            </svg>
          </p>
          <h3 className="bottomAnimation heading-tertiary">About Us</h3>
          <p className="bottomAnimation description">
            <strong>Justtry</strong> is a leading software development company
            that specializes in providing a full range of enterprise solutions
            to satisfy the particular needs of your company. We are more than
            just a team of skilled developers and designers; we are visionary
            creators who believe in transforming possibilities into realities.
          </p>
        </div>
        <div className="bottomAnimation works-text-box ">
          <p className="work-number">01</p>
          <h3 className="heading-tertiary">Redesign and Rebuild</h3>
          <p className="description">
            We specialize in website redesign and rebuild projects, helping
            businesses modernize and improve their online presence. Our team of
            expert developers and designers work closely with clients to create
            visually stunning and highly functional websites.
          </p>
        </div>
        <div className="bottomAnimation work-img-box">
          <img className="work-img" src={rebuildImg} alt="Design Logo" />
        </div>
        <div className="bottomAnimation work-img-box">
          <img className="work-img" src={ourTeam} alt="Design Logo" />
        </div>
        <div className="bottomAnimation works-text-box">
          <p className="work-number">02</p>
          <h3 className="heading-tertiary">Our Team</h3>
          <p className="description">
            Our team of skilled web developers has faced many challenges, but we
            always persevere and find solutions to overcome them. We are
            dedicated to delivering the best products and services to our
            clients.
          </p>
        </div>
      </div>
    </section>
  );
};

export default AboutUs;
