import React, { useEffect } from "react";
import Technology from "../../styles/assets/images/ServiceLogo/webdevelop.png";
import react from "../../styles/assets/images/FramworksHD/React.png";
import angular from "../../styles/assets/images/FramworksHD/Angular.png";
import js from "../../styles/assets/images/FramworksHD/js.png";
import solidity from "../../styles/assets/images/FramworksHD/solidity.avif";
import node from "../../styles/assets/images/FramworksHD/node-js.png";
import mongodb from "../../styles/assets/images/FramworksHD/mongodb.avif";

const OurDevelopment = () => {
  return (
    <section className="section-tech">
      <h3 className="heading-tertiary servicehead">
        Our Development Technologies
      </h3>
      <div className="container grid grid--2-cols">
        <div className="technologies">
          <img
            className="bottomAnimation tech-img"
            src={Technology}
            alt="Technology Images"
          />
        </div>
        <div className="grid--3-cols">
          <div className="gallery">
            <figure className="gallery-item">
              <img
                className="rightAnimation techstack-img"
                src={react}
                alt="Techstack Logos"
              />

              <figcaption className="bottomAnimation">React</figcaption>
            </figure>
            <figure className="gallery-item">
              <img
                className="rightAnimation techstack-img"
                src={angular}
                alt="Techstack Logos"
              />

              <figcaption className="bottomAnimation">Angular</figcaption>
            </figure>

            <figure className="gallery-item">
              <img
                className="rightAnimation techstack-img"
                src={js}
                alt="Techstack Logos"
              />

              <figcaption className="bottomAnimation">Express JS</figcaption>
            </figure>
            <figure className="gallery-item">
              <img
                className="rightAnimation techstack-img"
                src={solidity}
                alt="Techstack Logos"
              />
              <figcaption className="bottomAnimation">Solidity</figcaption>
            </figure>
            <figure className="gallery-item">
              <img
                className="rightAnimation techstack-img"
                src={node}
                alt="Techstack Logos"
              />
              <figcaption className="bottomAnimation">Node</figcaption>
            </figure>

            <figure className="gallery-item">
              <img
                className="rightAnimation techstack-img"
                src={mongodb}
                alt="Techstack Logos"
              />

              <figcaption className="bottomAnimation">mongo db</figcaption>
            </figure>
          </div>
        </div>
      </div>
    </section>
  );
};

export default OurDevelopment;
